/* eslint-disable react/jsx-no-target-blank */
import { useState, useEffect, useMemo, useRef } from 'react'
import { Link, useParams } from 'react-router-dom'
import { inject, observer } from 'mobx-react'
import { useAlert } from 'react-alert'
import OperateLiquidity from './Detail/OperateLiquidity'
import Tab from '../../components/Tab/Tab'
import apiProxy from "../../model/ApiProxy"
import { isCakeLP, isSushiLP, isEpochMining, isV3 } from "../../utils/utils"
import config from '../../config.json'
import useConfig from '../../hooks/useConfig'
import { DeriEnv, openConfigListCache } from '../../lib/web3js/index'
import './detail.less'
import './claim.less'
import backIcon from '../../assets/img/back.svg'
import BSC from '../../assets/img/BNB.svg'
import POLYGON from '../../assets/img/polygon-logo.svg'
import ETH from '../../assets/img/ethereum.svg'
import ARBITRUM from '../../assets/img/arbitrum-logo.svg'
import deri from './img/deri.svg'
import stakedLogo from './img/staked.svg'
import errorimg from '../../assets/img/ErrorImg.png'
import PoolInfo from './Detail/PoolInfo'
import TokenList from './Detail/TokenList'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import Spinner from '../../components/Spinner/Spinner'
import TimeSlider from '../../components/Slider/TimeSlider'
import PoolChart from './Detail/Chart'

const env = DeriEnv.get();
const { chainInfo } = config[env]
function Detail({ lang, wallet = {}, poolManager, getLang }) {
  const { version, chainId, address, type } = useParams();
  const alert = useAlert();
  const [nowToken, setNowToken] = useState()
  const [nowTokenId, setNowTokenId] = useState('')
  const [nowIsStaked, setNowIsStaked] = useState(false);
  const [poolConfig, setPoolConfig] = useState('')
  const [bTokenList, setBTokenList] = useState([])
  const [stakedList, setStakedList] = useState([])
  const [poolInfo, setPoolInfo] = useState({})
  const [poolDeri, setPoolDeri] = useState(0)
  const [poolEpochDeri, setPoolEpochDeri] = useState({})
  const config = useConfig();
  const [symbols, setSymbols] = useState('')
  const [walletBalance, setWalletBalance] = useState()
  const [nowApy, setNowApy] = useState({})
  const [startTimestamp, setStartTimestamp] = useState(0)
  const [endTimestamp, setEndTimestamp] = useState(0)
  const [nowTimestamp, setNowTimestamp] = useState(0)
  const [remainingTime, setRemainingTime] = useState('')
  const [claimPending, setClaimPending] = useState(false)
  const networkText = chainInfo[chainId] && chainInfo[chainId].name;
  const noWalletNowTokenRef = useRef()
  const WalletNowTokenRef = useRef()

  const networkLogo = (chainId) => {
    let src;
    let id = +chainId
    if (id === 56 || id === 97) {
      src = BSC
    } else if (id === 137) {
      src = POLYGON
    } else if (id === 1) {
      src = ETH
    } else if (id === 42161 || id === 421611) {
      src = ARBITRUM
    }
    return src
  }
  const onImgError = (event) => {
    event.target.src = errorimg
  }
  const getWalletBalance = async () => {
    if (wallet.isConnected() && +wallet.detail.chainId === + chainId) {
      let params = [wallet.detail.chainId, address, wallet.detail.account]
      let balance = null;
      setWalletBalance("")
      if (poolConfig.isLp) {
        balance = await apiProxy.request('getLpWalletBalance', params)
      } else {
        if (version === 'v2_lite_open') {
          await openConfigListCache.update()
        }
        if ((version === 'v3' || version === 'v3_lite') && nowToken) {
          params.push(nowToken)
          balance = await apiProxy.request('getBTokenBalance', params)
        } else {
          params.push(nowTokenId)
          balance = await apiProxy.request('getBTokenBalance', params)
        }
      }
      setWalletBalance(balance)
    }
  }
  const getPendingReward = async () => {
    let res = await apiProxy.request('getPendingReward', [wallet.detail.chainId, address, wallet.detail.account])
    if (res) {
      if (isEpochMining(address)) {
        setPoolEpochDeri(res)
        let amount = res.valid ? res.amount : 0
        setPoolDeri(amount)
      } else {
        setPoolDeri(res)
      }
    }

  }
  const afterTransaction = () => {
    getWalletBalance()
    getLiquidityInfo()
    getIsStaked()
    noWalletNowTokenRef.current = "";
    WalletNowTokenRef.current = ""

  }
  const tabSet = useMemo(() => ({
    [lang['add']]: {
      content: <OperateLiquidity chainId={chainId} getLang={getLang} poolConfig={poolConfig} isLp={type} wallet={wallet} afterTransaction={afterTransaction} version={version} bTokenId={nowTokenId} address={address} lang={lang} type='add' poolInfo={poolInfo} baseToken={nowToken} walletBalance={walletBalance} />,
      style: { background: "linear-gradient(200.74deg, rgba(97, 122, 255, 0.1) -4.65%, rgba(223, 229, 255, 0) 50.94%, rgba(85, 91, 237, 0.05) 105.4%)" },
      borderColor: "#203B60",
      icon: 'add-icon',
      iconWidth: '25',
      iconHeight: '25',
      inicon: 'inactive-add',
      direction: "tab-left"
    },
    [lang['remove']]: {
      content: <OperateLiquidity chainId={chainId} getLang={getLang} isLp={type} poolConfig={poolConfig} wallet={wallet} afterTransaction={afterTransaction} lang={lang} address={address} type='remove' bTokenId={nowTokenId} poolInfo={poolInfo} balance={poolInfo} baseToken={nowToken} version={version} />,
      style: { background: "linear-gradient(200.74deg, rgba(97, 122, 255, 0.1) -4.65%, rgba(223, 229, 255, 0) 50.94%, rgba(85, 91, 237, 0.05) 105.4%)" },
      borderColor: "#203B60",
      icon: 'remove-icon',
      iconWidth: '25',
      iconHeight: '25',
      iconMarginRight: '20',
      inicon: 'inactive-remove',
      direction: "tab-right",
    },
  }), [lang, getLang, poolConfig, type, wallet, version, nowTokenId, address, nowToken, walletBalance, poolInfo])

  const load = async () => {
    await poolManager.load()
    let pool = poolManager.pools;
    if (type === 'lp') {
      await poolManager.loadDoubleMingingPool();
      pool = poolManager.lpPools
    }
    if (version === 'v2_lite_open') {
      await poolManager.loadOpen();
      pool = poolManager.openPools
    }
    pool = pool.filter(p => p.address === address)
    pool = pool[0]
    let symbols = pool.symbols.map(item => {
      let arr = [];
      arr.push(item.symbol)
      return arr
    })
    symbols = symbols.join(',')
    setSymbols(symbols)
    setPoolConfig(pool)
    setBTokenList(pool.bTokens)
  }

  const getLiquidityInfo = async () => {
    let info = poolInfo
    let apy = poolInfo.apy / 100;
    let supplyApy = poolInfo.supplyApy;
    let xvsApy = poolInfo.xvsApy;
    let liquidity = poolInfo.liquidity;
    let apyMuList;
    let multiplier = poolInfo.multiplier;
    if (poolConfig) {
      if (version === "v2" && nowTokenId && noWalletNowTokenRef.current !== nowTokenId) {
        noWalletNowTokenRef.current = nowTokenId
        WalletNowTokenRef.current = nowTokenId
        let res = await poolConfig.getPoolInfo(address, chainId);
        if (res.bTokensApy && res.bTokensLiq) {
          let arr = bTokenList.map(item => {
            item.apy = (+(res.bTokensApy.filter(i => i.bTokenId === item.bTokenId)[0].apy) * 100)
          })
          apyMuList = res.bTokensApy.filter(p => p.bTokenId === nowTokenId);
          apy = apyMuList[0].apy;
          multiplier = apyMuList[0].multiplier
          liquidity = res.bTokensLiq.filter(p => p.bTokenId === nowTokenId);
          liquidity = liquidity[0].liquidity;
          info = {
            total: liquidity,
            apy: (+apy) * 100,
            supplyApy: +(supplyApy) * 100,
            multiplier: multiplier,
          }
        }
      } else if (version === "v2_lite" || (version === "v2" && type === "option")) {
        let res = await poolConfig.getPoolInfo(address, chainId);
        if (res.bTokensApy && res.bTokensLiq) {
          let arr = bTokenList.map(item => {
            item.apy = +(res.bTokensApy[0].apy) * 100
          })
          apy = res.bTokensApy[0].apy
          liquidity = res.bTokensLiq[0].liquidity
          info = {
            total: liquidity,
            apy: (+apy) * 100,
          }
        }
      } else if ((version === 'v3' || version === 'v3_lite') && nowToken && noWalletNowTokenRef.current !== nowToken) {
        noWalletNowTokenRef.current = nowToken
        let res = await poolConfig.getPoolInfo(address, chainId, version);
        WalletNowTokenRef.current = nowToken
        if (res.bTokensApy) {
          let arr = bTokenList.map(item => {
            if ((version === 'v3' || version === 'v3_lite') && !poolConfig.singleBtoken) {
              let apyArr = res.bTokensApy.find(i => i.bTokenSymbol === item.bTokenSymbol)
              if (apyArr) {
                item.apy = (+apyArr.apy) * 100
                item.supplyApy = ((+apyArr.supplyApy) * 100).toFixed(3)
                item.xvsApy = (+apyArr.xvsApy) * 100
              }
            } else if ((version === 'v3' || version === 'v3_lite') && poolConfig.singleBtoken) {
              item.apy = res.bTokensApy[0].apy * 100
            }
            return item
          })
          apyMuList = res.bTokensApy.filter(p => p.bTokenSymbol === nowToken);
          if (apyMuList.length) {
            apy = apyMuList[0].apy;
            supplyApy = apyMuList[0].supplyApy
            xvsApy = apyMuList[0].xvsApy
            multiplier = apyMuList[0].multiplier
          } else {
            apy = res.bTokensApy[0].apy
          }
          liquidity = res.liquidity;
          info = {
            total: liquidity,
            apy: (+apy) * 100,
            supplyApy: +(supplyApy) * 100,
            xvsApy: +(xvsApy) * 100,
            multiplier: multiplier,
          }
        }
      } else if (version !== "v3" && version !== "v2_lite" && version !== 'v3_lite' && version !== "v2") {
        let res = await poolConfig.getPoolInfo(address, chainId);
        if (res.bTokensApy && res.bTokensLiq) {
          let arr = bTokenList.map(item => {
            item.apy = +(res.bTokensApy[0].apy) * 100
            if (isSushiLP(address)) {
              item.lpApy = res.lpApy
            }
          })
          apy = res.bTokensApy[0].apy
          liquidity = res.bTokensLiq[0].liquidity
          info = {
            total: liquidity,
            apy: (+apy) * 100,
          }
        }
      }
      console.log("isConnectedBegin", info)
      setPoolInfo(info)
      if (wallet.isConnected() && +wallet.detail.chainId === +chainId) {
        if (version === "v2" && nowTokenId) {
          let res = await poolConfig.getLiquidityInfo(wallet, address, poolConfig.version, poolConfig.isLp, type, nowToken, nowTokenId)
          if (res) {
            info = res;
            info['apy'] = (+apy) * 100;
            info['multiplier'] = multiplier
          }
        } else if (version === "v2_lite" || (version === "v2" && type === "option")) {
          let res = await poolConfig.getLiquidityInfo(wallet, address, poolConfig.version, poolConfig.isLp, type, nowToken)
          if (res) {
            info = res;
            info['apy'] = (+apy) * 100;
            info['multiplier'] = multiplier
          }
        } else if ((version === 'v3' || version === 'v3_lite') && nowToken && (WalletNowTokenRef.current === nowTokenId || WalletNowTokenRef.current === nowToken)) {
          let res = await poolConfig.getLiquidityInfo(wallet, address, poolConfig.version, poolConfig.isLp, type, nowToken, nowTokenId)
          if (res) {
            info = res;
            if ((version === 'v3' || version === 'v3_lite') && !poolConfig.singleBtoken) {
              info['supplyApy'] = +(supplyApy) * 100
              info['xvsApy'] = +(xvsApy) * 100;
            }
            info['apy'] = (+apy) * 100;
            info['multiplier'] = multiplier
          }
        } else if (version !== "v3" && version !== "v2_lite" && version !== 'v3_lite' && version !== "v2") {
          let res = await poolConfig.getLiquidityInfo(wallet, address, poolConfig.version, poolConfig.isLp, type, nowToken)
          if (res) {
            info = res;
            info['apy'] = (+apy) * 100;
            info['multiplier'] = multiplier
          }
        }
        console.log("isConnectedAfter", info)
        setPoolInfo(info)

      }
    }
  }

  const getIsStaked = async () => {
    if (wallet.isConnected() && poolConfig && +wallet.detail.chainId === + chainId) {
      let res = await poolConfig.getIsStaked(wallet, poolConfig)
      if (res.bTokens) {
        let arr = poolConfig.bTokens.map((item) => {
          if (version === 'v2' && type !== 'option') {
            item.staked = res.bTokens.filter(p => p.bTokenId === item.bTokenId)[0].isStaked
          } else if (version === 'v3' || version === 'v3_lite') {
            let stakeLi = res.bTokens.filter(p => p.bTokenSymbol === item.bTokenSymbol)
            item.staked = stakeLi.length ? stakeLi[0].isStaked : false

          } else {
            item.staked = res.isStaked
          }
          return item
        })
        let obj = [];
        let stakedArr = [];
        arr.forEach((item, index) => {
          if (item.staked) {
            obj.push(item);
          } else {
            stakedArr.push(item)
          }
        })
        stakedArr.unshift(...obj)
        setStakedList(stakedArr)
      } else {
        if (version === 'v3' || version === 'v3_lite') {
          setNowToken(poolConfig.bTokens[0].bTokenSymbol)
        } else {
          let bToken0;
          if (isCakeLP(address)) {
            bToken0 = 'PANCAKE-LP'
            setNowToken(bToken0)
          } else if (isSushiLP(address)) {
            bToken0 = 'DERI-USDT SLP'
            setNowToken(bToken0)
          } else {
            setNowToken(poolConfig.bTokens[0].bTokenSymbol)
            setNowTokenId(poolConfig.bTokens[0].bTokenId)
          }

        }
      }
    } else if (poolConfig && poolConfig.bTokens.length) {
      if (version === 'v3' || version === 'v3_lite') {
        setNowToken(poolConfig.bTokens[0].bTokenSymbol)
      } else {
        let bToken0;
        if (isCakeLP(address)) {
          bToken0 = 'PANCAKE-LP'
          setNowToken(bToken0)
        } else if (isSushiLP(address)) {
          bToken0 = 'DERI-USDT SLP'
          setNowToken(bToken0)
        } else {
          setNowToken(poolConfig.bTokens[0].bTokenSymbol)
          setNowTokenId(poolConfig.bTokens[0].bTokenId)
        }

      }
    }
  }

  const checkoutBaseToken = async (bTokenId, bTokenSymbol) => {
    if ((nowTokenId && bTokenId === nowTokenId) || (nowToken && bTokenSymbol === nowToken)) {
      return false;
    }
    let now;
    if (version !== 'v3' && version !== 'v3_lite') {
      now = poolConfig.bTokens.filter(b => b.bTokenId === bTokenId)
    } else {
      now = poolConfig.bTokens.filter(b => b.bTokenSymbol === bTokenSymbol)
    }
    setPoolInfo({})
    setNowTokenId(bTokenId)
    setWalletBalance('')
    setNowToken(now[0].bTokenSymbol)
    setNowIsStaked()
  }
  const claimDeri = async () => {
    if (+poolDeri === 0) {
      alert.error(lang['no-deri-to-claim-yet']);
      return;
    }
    if (isEpochMining(address)) {
      let now = parseInt(Date.now() / 1000) % (3600 * 8);
      if (now < 1800) {
        alert.error(lang['claiming-DERI-is-disabled-during-first-30-minutes-of-each-epoch']);
        return;
      }
    }
    setClaimPending(true)
    let res = await apiProxy.request('claimReward', [wallet.detail.chainId, address, wallet.detail.account], {
      includeResponse: true,
      write: true,
      subject: lang['claim-deri-title'],
      title: {
        processing: lang['claim-deri-processing'],
        success: lang['claim-reward-executed'],
        error: lang['claim-reward-failed'],
      },
      content: {
        success: lang['claim-deri-reward'],
        error: lang['transaction-failed'],
      }
    })
    getPendingReward()
    setClaimPending(false)
    if (res.success) {
      setPoolDeri(0)
      return true;
    }
  }

  useEffect(() => {
    if (wallet.detail.chainId && wallet.detail.chainId !== chainId) {
      wallet.switchNetwork(config[chainId])
    }
    if (wallet.isConnected()) {
      let interval = window.setInterval(getPendingReward, 1000 * 10 * 3);
      getPendingReward()
      return () => clearInterval(interval);
    }
  }, [wallet.detail.chainId, wallet.detail.account])
  useEffect(() => {
    load()
  }, [])
  useEffect(() => {
    if (bTokenList.length && (nowToken || nowTokenId)) {
      let apyObj = {}
      if (version !== 'v2') {
        let apyList = bTokenList.filter(p => p.bTokenSymbol === nowToken)
        apyObj = apyList[0]
      } else if (version === 'v2') {
        let apyList = bTokenList.filter(p => p.nowTokenId === nowTokenId)
        apyObj = apyList[0]
      }
      setNowApy(apyObj)
      let now;
      if (version === 'v2' && type !== 'option') {
        if (nowTokenId) {
          now = bTokenList.filter(p => p.bTokenId === nowTokenId)[0].staked
        }
      } else if (type !== 'lp') {
        now = bTokenList.filter(p => p.bTokenSymbol === nowToken)[0].staked
      }
      setNowIsStaked(now)
    }
  }, [bTokenList, nowToken, nowTokenId])


  useEffect(() => {
    if (stakedList.length) {
      let bToken0 = stakedList[0].bTokenSymbol
      if (isCakeLP(address)) {
        bToken0 = 'PANCAKE-LP'
      } else if (isSushiLP(address)) {
        bToken0 = 'DERI-USDT SLP'
      }
      let bTokenId = stakedList[0].bTokenId
      if (version === 'v3' || version === 'v3_lite') {
        setNowToken(bToken0)
      } else {
        setNowToken(bToken0)
        setNowTokenId(bTokenId)
      }
      setBTokenList(stakedList)
    }
  }, [stakedList])

  useEffect(() => {
    getLiquidityInfo()
    getWalletBalance()
  }, [nowTokenId, poolConfig, wallet.detail.account, nowToken, wallet.detail.chainId])
  useEffect(() => {
    getIsStaked()
  }, [poolConfig, wallet.detail.account, wallet.detail.chainId])
  useEffect(() => {
    if ((version !== 'v2_lite_open' && poolConfig.rewarded) && isEpochMining(address)) {
      let interval = null;
      //计数器
      interval = window.setInterval(() => {
        let period = 3600 * 8;
        let current = parseInt(Date.now() / 1000);
        let epochBegin = parseInt(current / period) * period;
        let dis = (epochBegin + period - current);
        let h = parseInt(dis / 3600);
        let m = parseInt((dis % 3600) / 60)
        let s = parseInt(dis % 60)
        setStartTimestamp(epochBegin)
        setEndTimestamp(epochBegin + period)
        setNowTimestamp(current)
        setRemainingTime(`${h} ${lang['h']} ${m} ${lang['m']} ${s} ${lang['s']}`);
      }, 1000);
      return () => {
        interval && clearInterval(interval);
      };
    }


  }, [poolConfig]);
  return (
    <div className='pool-detail'>
      <div className='back-pool'>
        {/* <Link to={(address === '0x08aD0E0b4458183758fC5b9b6D27c372965fB780' || address === '0x66f501dda450C8978c4A1115D7b2A7FAa7702F05' || address === '0x574022307e60bE1f07da6Ec1cB8fE23d426e5831' || address === '0x4ad5cb09171275A4F4fbCf348837c63a91ffaB04') ? '/retired' : '/pool'}> */}
        <div className='icon-back' onClick={() => { window.history.back(-1) }}>
          <img alt='' src={backIcon} />
          <span>{lang['back']}</span>
        </div>
        {/* </Link> */}
      </div>
      <div className='pool-deatail-info'>
        <div className='pool-detail-basetoken-info'>
          <div className='pool-info-text'>
            <div className='pool-network-type'>
              <div className='network-logo'>
                <img alt='' src={networkLogo(chainId)} />
              </div>
              <div className='pool-type-network-text'>
                <div className='pool-type-text'>
                  {address !== '0xD2D950e338478eF7FeB092F840920B3482FcaC40' && <>
                    {lang[poolConfig.type]} {lang[poolConfig.zone]}
                  </>}
                  {address === "0x1eF92eDA3CFeefb8Dae0DB4507f860d3b73f29BA" && <>
                    DERI-based
                </>}
                  {address === '0xD2D950e338478eF7FeB092F840920B3482FcaC40' && <>
                    {lang[poolConfig.zone]}
                  </>}
                </div>
                <div className='pool-network-text'>
                  {networkText === 'BSC' ? "BNB Chain" : networkText}
                </div>
              </div>
            </div>
            <div className='pool-des'>
              {type !== 'lp' && lang['amm-pool-des']}
              {version === 'clp' && <>
              {lang['deri-pancake-pool-one']} <a rel='norelerror' target='_blank' href="https://pancakeswap.finance/add/0xe60eaf5A997DFAe83739e035b005A33AfdCc6df5/0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56" >DERI-BUSD </a> {lang['deri-pancake-pool-two']}
              </>}
              {version === 'slp' && <>
                {lang['deri-sushi-pool']}
              </>}
              <br></br>
              {type !== 'lp' ? <a rel='norelerror' target='_blank' href='https://docs.deri.io/library/faqs/mining-faq#is-the-amm-liquidity-mining-on-deri-pools-risk-free'><span className='learn-more'> {lang['learn-more']}</span></a> : <a rel='norelerror' target='_blank' href='https://docs.deri.io/library/faqs/mining-faq#is-dex-liquidity-mining-on-sushiswap-sushiswap-onsen-and-pancakeswap-deri-risk-free'><span className='learn-more'> {lang['learn-more']}</span></a>}
            </div>
          </div>
          <TokenList lang={lang} pool={poolConfig} chainId={chainId} address={address} checkoutBaseToken={checkoutBaseToken} nowToken={nowToken} bTokenList={bTokenList} version={version} type={type} />
        </div>
        <div className='pool-detail-add-remove-info'>
          <div className='mining-deri-liquidity'>
            <div className='btoken-liquidity'>
              {type !== 'lp' && <>
                <div className={`btoken-logo ${nowToken}`}>
                  {version === 'v2_lite_open' && <>
                    <img alt='' onError={onImgError} src={`https://raw.githubusercontent.com/deri-io/deri-open-zone/main/img/${nowToken}.png`} />
                  </>}
                </div>
                <div className='manage-stake'>
                  <div className='manage'>
                    {lang['manage']} {nowToken} {lang['liquidity']}
                  </div>
                  {nowIsStaked && <div className='stake'>
                    <img src={stakedLogo} alt='' />
                    {lang['staked']}
                  </div>}
                </div>
              </>}
              {type === 'lp' && bTokenList.length && <>
                <div className='lp-btoken-logo'>
                  <div className={`btoken-logo ${bTokenList[0].bTokenSymbol}`}></div>
                  <span className='conect-logo'>+</span>
                  <div className={`btoken-logo ${bTokenList[1].bTokenSymbol}`}></div>
                </div>
                <div className='manage-stake'>
                  <div className='manage'>
                    {lang['manage']} {nowToken} {lang['liquidity']}
                  </div>
                  {nowIsStaked && <div className='stake'>
                    <img src={stakedLogo} alt='' />
                    {lang['staked']}
                  </div>}
                </div>
              </>}
            </div>
            <div className='deri-current-epoch'>
              {(version !== 'v2_lite_open' && poolConfig.rewarded) && <>
                {isEpochMining(address) && address !== '0x26bE73Bdf8C113F3630e4B766cfE6F0670Aa09cF' && <>
                  <div className='deri-num-slider'>
                    <div className='deri-cliam-remaining'>
                      {remainingTime}
                    </div>
                    <div className='deri-claim-slider'>
                      <TimeSlider startTimestamp={startTimestamp} nowTimestamp={nowTimestamp} endTimestamp={endTimestamp} />
                    </div>
                    <div className="deri-claim-pool-num">
                      {getLang('deri-claim-pool-num', { num: +poolEpochDeri.lp ? (+poolEpochDeri.lp).toFixed(4) : 0 })}
                    </div>
                  </div>

                </>}
                <div className={isEpochMining(address) && address !== '0x26bE73Bdf8C113F3630e4B766cfE6F0670Aa09cF' ? 'mining-pool-claim epoch-pool' : 'mining-pool-claim'}>
                  <div className='mining-claim-info'>
                    <span> {lang['you-have']}</span>
                    <span className={`${+poolDeri === 0 ? 'no-deri' : ""} mining-pool-num`} onClick={claimDeri}>
                      {claimPending && <Spinner />}
                      <DeriNumberFormat firstTime={false} value={poolDeri} decimalScale={2} />
                      <img src={deri} alt="deri" />
                    </span>
                    <span>{lang['claim-in-pool']}</span>
                  </div>
                </div>
              </>}
            </div>
          </div>
          <div className='mining-add-remove-liquidity'>
            <PoolInfo wallet={wallet} nowApy={nowApy} poolInfo={poolInfo} chainId={chainId} address={address} nowToken={nowToken} lang={lang} version={version} type={type} poolConfig={poolConfig} symbols={symbols} />
            <div className='add-remove-liquidity'>
              <Tab tabSet={tabSet} iconMarginRight='20' width={379} size={18} inactiveBgColor='#0c192d' icon='' borderBottom={true} divider={true} />
            </div>
          </div>
        </div>
      </div>
      {(version === "v3" || version === "v3_lite") && <PoolChart address={address} chainId={chainId} />}
    </div>
  )
}
export default inject('wallet', 'poolManager')(observer(Detail))