import { useState } from "react"
import Filter from "../../Info/Filter"
import AreaChart from '../../../components/AreaChart/AreaChart'
import PieChart from '../../../components/PieChart/PieChart'
import "./poolChart.less"
export default function PoolChart({ address, chainId }) {
  const [getLpNetValue, setGetLpNetValue] = useState(`${process.env.REACT_APP_INFO_HTTP_URL}/get_liquidity_netvalue?pool=${address}`)
  const [getTokens, setGetTokens] = useState(`${process.env.REACT_APP_INFO_HTTP_URL}/get_tokens?pool=${address}`)
  return (
    <div className="pool-chart-box">
      <div className='graph lp-graph netValue'>
        <Filter url={getLpNetValue} className='line-chart-filter' onChange={url => setGetLpNetValue(url)} cycleArr={['7D', '15D', "ALL"]} />
        <AreaChart title='LP Net Value' isTime={true} url={getLpNetValue} domain={true} yMin={0.0001} yMax={0.0001} decimalScale={4} height={350} tip={+chainId === 56 ? "This is the BUSD value you would have as of today if you had provided 1BUSD as liquidity at the pool launch. It contains the trading PnL from Deri Protocol and the interest yield from Venus. Please note that your mining harvests of DERI and XVS are NOT included in this Net Value. If you provided tokens other than BUSD (i.e. BNB), the net value might be slightly different as the interest rate depends on tokens." : "This is the USDC value you would have as of today if you had provided 1USDC as liquidity at the pool launch. It contains the trading PnL from Deri Protocol and the interest yield from AAVE. Please note that your mining harvests of DERI are NOT included in this Net Value. If you provided tokens other than USDC (i.e. BTC), the net value might be slightly different as the interest rate depends on tokens."} />
      </div>
      <div className='graph token-graph tokens'>
        <Filter className='chart-filter' url={getTokens} onChange={url => setGetTokens(url)} cycleArr={['TRADER', 'LP']} />
        <PieChart title='Top Margin/Liquidity' url={getTokens} />
      </div>
    </div>
  )
}