import { useEffect, useState } from "react"
import { hide } from 'react-functional-modal'
import closeModalIcon from '../../assets/img/close-modal-icon.svg'
import classNames from "classnames"
import Button from "../../components/Button/Button"
import Input from "../../components/Input/Input"
import Icon from "../../components/Icon/Icon"
import DeriNumberFormat from "../../utils/DeriNumberFormat"
import { bg } from '../../lib/web3js/index';
import apiProxy from '../../model/ApiProxy'
import './liquidityGuidance.less'
import Loading from './Loading'
export default function LiquidityGuidance({ wallet, alert }) {
  const [index, setIndex] = useState(0)
  const [pending, setPending] = useState(false)
  const [isRemove, setIsRemove] = useState(false)
  const [isApprove, setIsApprove] = useState(false)
  const [isAdd, setIsAdd] = useState(false)
  const [shares, setShares] = useState()
  const [deriNum, setDeriNum] = useState()
  const [balance, setBalance] = useState()
  const [newShare, setNewShare] = useState()
  const [label, setLabel] = useState("REMOVE LIQUIDITY")
  const getinfo = async () => {
    let res = await apiProxy.request("getLiquidityInfo", [wallet.detail.chainId, "0x26bE73Bdf8C113F3630e4B766cfE6F0670Aa09cF", wallet.detail.account, 0])
    const share = res.shares ? bg(res.shares) : bg(0)
    const total = share.isNaN() ? bg(0) : share.multipliedBy(res.shareValue)
    setShares(share.toString())
    setDeriNum(total.toFixed(4))
  }

  const getNewInfo = async () => {
    let res = await apiProxy.request("getLiquidityInfo", [wallet.detail.chainId, "0x1eF92eDA3CFeefb8Dae0DB4507f860d3b73f29BA", wallet.detail.account, "DERI"])
    console.log("getLiquidityInfo", res)
    const share = res.shares ? bg(res.shares) : bg(0)
    setNewShare(share.toString())
  }
  const getBalance = async () => {
    let res = await apiProxy.request("getBTokenBalance", [wallet.detail.chainId, "0x26bE73Bdf8C113F3630e4B766cfE6F0670Aa09cF", wallet.detail.account, 0])
    console.log("getBTokenBalance", res)
    setBalance(res)
  }

  const Operate = async () => {
    let res;
    setPending(true)
    if (index === 0) {
      let param = [wallet.detail.chainId, "0x26bE73Bdf8C113F3630e4B766cfE6F0670Aa09cF", wallet.detail.account, shares, undefined, true]
      res = await apiProxy.request('removeLiquidity', param, {
        includeResponse: true,
        write: true,
        subject: "REMOVE LIQUIDITY"
      })
      getBalance()
      setPending(false)
      if (!res || !res.success) {
        if (res.response.transactionHash === "") {
          return false;
        }
        alert.error(`Transaction Failed  : ${res.response.error}`, {
          timeout: 300000,
          isTransaction: true,
          transactionHash: res.response.transactionHash,
          link: `${wallet.blockExploreUrl}/tx/${res.response.transactionHash}`,
          title: "Remove Liquidity Failed"
        })
        return false;
      } else {
        setIndex(index + 1)
        setIsRemove(true)
        alert.success(`Remove DERI Liquidity`, {
          timeout: 8000,
          isTransaction: true,
          transactionHash: res.response.data.transactionHash,
          link: `${wallet.blockExploreUrl}/tx/${res.response.data.transactionHash}`,
          title: "Remove Liquidity Executed"
        })
      }
      return true;

    } else if (index === 1) {
      let params = [wallet.detail.chainId, "0x1eF92eDA3CFeefb8Dae0DB4507f860d3b73f29BA", wallet.detail.account, "DERI"]
      res = await apiProxy.request('unlock', params, { write: true, subject: "APPROVE" });
      setPending(false)
      if (res) {
        if (res.status) {
          setIndex(index + 1)
          setIsApprove(true)
          alert.success(`Approve DERI`, {
            timeout: 8000,
            isTransaction: true,
            transactionHash: res.transactionHash,
            link: `${wallet.blockExploreUrl}/tx/${res.transactionHash}`,
            title: 'Approve Executed'
          })
        } else {
          if (res.transactionHash === "") {
            return false;
          }
          alert.error(`Transaction Failed ${res.error}`, {
            timeout: 300000,
            isTransaction: true,
            transactionHash: res.transactionHash,
            link: `${wallet.blockExploreUrl}/tx/${res.transactionHash}`,
            title: 'Approve Failed'
          })
        }
      }
    } else if (index === 2) {
      let param = [wallet.detail.chainId, "0x1eF92eDA3CFeefb8Dae0DB4507f860d3b73f29BA", wallet.detail.account, balance, "DERI"]
      res = await apiProxy.request('addLiquidity', param, {
        includeResponse: true,
        write: true,
        subject: "ADD LIQUIDITY"
      })
      setPending(false)
      getBalance()
      getNewInfo()
      if (!res || !res.success) {
        if (res.response.transactionHash === "") {
          return false;
        }
        alert.error(`Transaction Failed  : ${res.response.error}`, {
          timeout: 300000,
          isTransaction: true,
          transactionHash: res.response.transactionHash,
          link: `${wallet.blockExploreUrl}/tx/${res.response.transactionHash}`,
          title: "Add Liquidity Failed"
        })
        return false;
      } else {
        setIsAdd(true)
        hide("LiquidityGuidance")
        alert.success(`Add DERI Liquidity`, {
          timeout: 8000,
          isTransaction: true,
          transactionHash: res.response.data.transactionHash,
          link: `${wallet.blockExploreUrl}/tx/${res.response.data.transactionHash}`,
          title: "Add Liquidity Executed"
        })
      }
      return true;
    }

  }
  useEffect(() => {
    if (wallet.isConnected()) {
      getinfo()
      getBalance()
    }
  }, [wallet])
  useEffect(() => {
    if (index === 1) {
      setLabel("APPROVE")
    } else if (index === 2) {
      setLabel("ADD LIQUIDITY")
    }
  }, [index])
  return (
    <div className="guidance-modal">
      <div className='title-close'>
        <div className="title-text">
          Migrate Liquidity
        </div>
        <div className="close">
          <img src={closeModalIcon} alt='' onClick={() => hide('LiquidityGuidance')} />
        </div>
      </div>
      <div className='step-box'>
        <div className={classNames("remove", { "pending": index === 0 && pending, "success": index !== 0 && isRemove })}>
          {index === 0 && pending && <Loading borderColor="rgba(89, 174, 153, 0.5)" bgColor="rgba(89, 174, 153, 0.3)" />}
          {index !== 0 && isRemove && <Icon token="Guidance-success" />}
          REMOVE
        </div>
        <div className={classNames('hr', { "hr-success": index !== 0 && isRemove })}></div>
        <div className={classNames("approve", { "pending": index === 1 && pending, "success": index === 2 && isApprove })}>
          {index === 1 && pending && <Loading borderColor="rgba(89, 174, 153, 0.5)" bgColor="rgba(89, 174, 153, 0.3)" />}
          {index === 2 && isApprove && <Icon token="Guidance-success" />}
          APPROVE
        </div>
        <div className={classNames('hr', { "hr-success": index === 2 && isApprove })}></div>
        <div className={classNames("add", { "pending": index === 2 && pending, "success": isAdd })}>
          {index === 2 && pending && <Loading borderColor="rgba(89, 174, 153, 0.5)" bgColor="rgba(89, 174, 153, 0.3)" />}
          {isAdd && <Icon token="Guidance-success" />}
          ADD
        </div>
      </div>
      <div className='guidance-info'>
        <div className='now-step'>
          {index === 0 && " Step 1: remove liquidity from the retired Future Inno Pool"}
          {index === 1 && " Step 2: approve DERI to the new Inno Pool"}
          {index === 2 && " Step 3: add DERI to the new Inno Pool"}
        </div>
        {index === 0 && <div className="pool-type">
          <div className='pool-title'>Pool</div>
          <div className='pool-type-text'> Future Inno</div>
        </div>}
        <div className="balance-box">
          <div className='balance-title'>Your Staked Balance</div>
          <div className='balance-num'>
            {index === 0 && <><DeriNumberFormat value={shares} decimalScale={2} /> shares ({<DeriNumberFormat value={deriNum} />} DERI)</>}
            {index !== 0 && <DeriNumberFormat value={isAdd ? newShare : balance} suffix=" DERI" />}
          </div>
        </div>
        <div className='operate-box'>
          <div className="input-box">
            <Input inputWidth="332px" disabled={index === 0} decimal={18} unit={index === 0 ? "SHARES" : "DERI"} value={index === 0 ? shares : balance} onChange={value => setBalance(value)} />
          </div>
          <Button width="332" disabled={index === 2 ? +balance > 0 ? false : true : false} label={label} onClick={Operate} fontSize='18' />
        </div>
      </div>
    </div>
  )
}