import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { AreaChart, XAxis, YAxis, Tooltip, Area, CartesianGrid, ResponsiveContainer } from 'recharts'
import dateFormat from 'dateformat';
import RechartTooltip from '../RechartTooltip/RechartTooltip';
import { dataFormater } from '../../utils/utils';

export default function RechartArea({ url, title, domain = false, yMin = 0, yMax = 0, decimalScale = 2, tip, height = 300,isTime=false }) {
  const [data, setData] = useState(null)
  const [lastItem, setLastItem] = useState({})
  const [parentRect, setParentRect] = useState({})
  const [Ydomain, setYdomain] = useState([0, 'auto'])
  const loadData = async (url) => {
    const res = await axios.get(url)
    let data = []
    if (res.status === 200 && Array.isArray(res.data.data)) {
      data = res.data.data.sort((item1, item2) => {
        if (item1.timestamp > item2.timestamp) {
          return 1
        } else if (item1.timestamp < item2.timestamp) {
          return -1
        } else {
          return 0
        }
      })
      if (res.data.current) {
        setLastItem(res.data.current)
      } else if (data.length > 0) {
        setLastItem(data[data.length - 1])
      }
      if (domain) {
        let min = Math.min.apply(Math, data.map(function (o) {
          return o.value.toFixed(decimalScale)
        }
        ))
        let max = Math.max.apply(Math, data.map(function (o) {
          return o.value.toFixed(decimalScale)
        }
        ))
        setYdomain([+(min - yMin).toFixed(decimalScale), +(max + yMax).toFixed(decimalScale)])
      }
    }
    setData(data.map(d => ({ time: d.timestamp, value: Number(d.value) })))
  }
  const tickFormatter = tick => {
    if (tick && tick !== 'auto') {
      return dateFormat(new Date(tick * 1000), 'mm-dd')
    } else {
      return ''
    }
  }


  useEffect(() => {
    const rect = document.querySelector('.graph').getBoundingClientRect()
    setParentRect(rect)
    loadData(url);
  }, [url])

  return (data ?
    <ResponsiveContainer height={height}>
      <AreaChart width={parentRect.width} data={data}>
        <defs>
          <linearGradient id="colorArea" x1="0" y1="1" x2="0" y2="0" >
            <stop offset="10%" stopColor="#2772F0" stopOpacity={0.1} />
            <stop offset="90%" stopColor="#2772f0" stopOpacity={0.3} />
          </linearGradient>
        </defs>

        <CartesianGrid
          vertical={false}
          strokeDasharray="9"
          stroke="#203B60"
        />
        <XAxis dataKey="time" axisLine={false} tickLine={false} tick={{ fill: '#fff', fontSize: '14' }} padding={{ left: 12, right: 14 }} allowDataOverflow={false} tickFormatter={tickFormatter} />
        <YAxis dataKey='value' minTickGap={10} domain={Ydomain} tickLine={false} axisLine={false} tickFormatter={dataFormater} tick={{ fill: '#fff', fontSize: '14' }} padding={{ top: 65 }} />
        <Tooltip position={{ x: 16, y: -106 }} cursor={false} content={<RechartTooltip isTime={isTime} title={title} lastItem={lastItem} decimalScale={decimalScale} tip={tip} />} />
        <Area type="monotone" dataKey="value" stroke="#3756CD" fillOpacity={0.5} strokeWidth={3} fill="url(#colorArea)" />
      </AreaChart>
    </ResponsiveContainer>
    : null)
}